.Question{
    padding: 96px 500px 108px 405px;
    h1{
        font-size: $fz48;
        line-height: 1.45;
        padding-left: 16px;
        font-weight: bold;
        border-left: 8px solid #aa0601;        
    }
    h2{
        font-size: $fz32;
        line-height: 1.4;
        font-weight: bold;
        margin-top: 60px;
        margin-bottom: 36px;
    }
    .QA{
        h3{
            font-size: $fz20;
            line-height: 1.4;
            font-weight: bold;
        }
        p{
            font-weight: bold;
            line-height: 1.5;
            margin-top: 16px;
            margin-bottom: 32px;
            padding-left: 92px;
        }
    }
    @media (max-width:768px){
        padding: 60px 39px 96px 39px;
    }
    @media (max-width:376px){
        padding: 36px 15px 56px 15px;
        h1{
            font-size: $fz28;
            line-height: 1.4;
            padding-left: 8px;
            border-left: 4px solid #aa0601;
        }
        h2{
            font-size: $fz20;
            line-height: 1.45;
            margin-top: 24px;
            margin-bottom: 24px;
        }
        .QA{
            h3{
                font-size: 16px;
                line-height: 1.5;
            }
            p{
                padding-left: 0;
                margin-top: 8px;
                margin-bottom: 20px;
                font-size: 14px;
            }
            p:nth-child(2){
                display: none;
            }
        }
    }
}